<style lang="stylus" rel="stylesheet/stylus">
  .line
    text-align:center
  .layout
    .left
      position fixed
      top 0
      bottom 0
      left 0
    .right
      position fixed
      top 0
      right 0
      bottom 0
      background-color #f5f7f9
      .layout-header:after
        position absolute
        content ""
        left 0
        right 0
        bottom 0
        height 1px
        background #ddd
      .layout-header
        position absolute
        width 100%
        height 60px
        background #fff
        .layout-header-title
          position absolute
          height 60px
          line-height 60px
          left 20px
          font-size 20px
          font-weight bold
        .el-dropdown
          position absolute
          right 50px
          .user-info
            cursor pointer
            .name
              display inline-block
              font-size 15px
              color #999
              line-height 60px
              height 60px
              span
                padding-left 5px
                color #324157
      .layout-content
        position fixed
        top 60px
        bottom 0
        width 100%
        border-radius 4px
        box-sizing border-box
        & > div:first-child
          overflow-y:auto;
          height calc(100% - 26px)
          .el-breadcrumb
            padding 10px 15px 0
          .main
            margin 15px
            padding 25px 10px 15px 10px
            min-height calc(100% - 93px)
            background #fff
            overflow auto
            box-shadow 0 2px 2px 0 rgba(7, 17, 27, .1)
            .el-select,.el-date-editor.el-input
              width 100%
            .header
              height 60px
              line-height 60px
            .page
              margin-top 10px
              text-align right
            .el-table
              max-height 448px
              .el-table__empty-text
                left calc(50vw - 18px)
            .el-table+.el-row--flex,.el-row--flex+.el-row--flex
              margin-top 10px
            .el-table__body-wrapper
              .el-table__body tr
                cursor pointer
              .current-row>td
                background #48576a
                color white
              .cell
                white-space nowrap
                word-break keep-all
            .el-row .el-dialog .ofdiv
              height calc(100vh - 163px)
              overflow-y auto
            .el-row .el-dialog .el-tabs
              margin-top -60px
              .el-tabs__header
                width 80%
                margin-left 10%
                text-align center
                .el-tabs__nav
                  display inline-block
                  float none
                  .el-tabs__item
                    font-size 15px
              .el-tabs__content
                height calc(100vh - 163px)
                border 1px solid #dfe6ec
                overflow-y auto
        .layout-copy
          position absolute
          top calc(100% - 26px)
          width 100%
          padding 7px 0
          text-align center
          color #9ea7b4
          font-size 12px
  .el-popover p
    padding 10px 0 20px 0
    font-size 16px
    letter-spacing 3px
    color #20A0FF
    text-align center
  .el-input.is-disabled input.el-input__inner
    background-color #F9FAFC
    color #333
    border none
</style>

<template>
  <div class="layout">
    <el-row type="flex">
      <!--<el-col :span="4" class="left">-->
        <!--<my-menu></my-menu>-->
      <!--</el-col>-->
      <el-col :span="24" class="right">
        <div class="layout-header">
          <div class="layout-header-title">{{title}}-{{subtitle}}</div>
          <el-dropdown @command="handleCommand">
            <div class="user-info">
              <span class="name">用户:<span>{{guiydaih}}</span>
                <i class="el-icon-caret-bottom el-icon--right"></i>
              </span>
            </div>
            <el-dropdown-menu slot="dropdown">
              <br>
              <el-form class="el-row" ref="form" :model="form" label-width="80px" label-align="left">
                <el-form-item class="el-col el-col-22 el-col-offset-1" label="登出密码" prop="psword">
                  <el-input v-model="form.psword" type="password"></el-input>
                </el-form-item>
              </el-form>
              <el-dropdown-item command="exit" align="center" v-show="form.psword!=''" style="color:#ff6d6d">退出</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
        <div class="layout-content">
          <router-view></router-view>
          <div class="layout-copy">
            2019 &copy; 南京银行
          </div>
        </div>
      </el-col>
    </el-row>
  </div>

</template>

<script>
  import myMenu from 'common/Menu'
  import util from '../../assets/js/util'
  import ElIcon from "../../../node_modules/element-ui/packages/icon/src/icon";
  export default {
    data() {
      return {
        form: {
          psword: ''
        },
        title:'南京银行',
        subtitle:'考评系统',
        guiydaih: '柜员代号',
        yngyjigo: '营业机构',
        guiyxinm: '柜员姓名',
        joyizblb: '交易组别列表',
        jiaosedm: '柜员角色代码',
        sqzblieb: '授权组别列表',
        guiyjbie: '柜员级别',
        date: '日期',
      }
    },
    components: {
      ElIcon, myMenu
    },
    methods: {
      handleCommand(command) {
        if (command === 'exit') {
          util.authcode = null
          util.removeSession('authcode')
          this.$router.push('/login')
        }
      }
    }
  }
</script>



