          <el-tooltip effect="dark" content="理财产品销售区域查询维护" placement="right"><el-menu-item index="8035">[8035]理财产品销售区域查询维护</el-menu-item></el-tooltip>
          <el-tooltip effect="dark" content="理财产品额度信息查询维护" placement="right"><el-menu-item index="8014">[8014]理财产品额度信息查询维护</el-menu-item></el-tooltip>
