      </div>
    </el-menu>
  </div>
</template>

<script>
  export default{
    data(){
      return {
        mission: [],
        state1: ''
      };
    },
    methods: {
      querySearch(queryString, cb) {
        var mission = this.mission;
        var results = queryString ? mission.filter(this.createFilter(queryString)) : mission;
        if (results.length == 1){
          this.handleSelect(results[0]);
        }
        // 调用 callback 返回建议列表的数据
        cb(results);
      },
      createFilter(queryString) {
        return (mission) => {
          return (mission.value.indexOf(queryString.toLowerCase()) > -1);
        };
      },
      handleSelect(queryString){
        var path = queryString.value.slice(1,5);
        this.$router.push('/layout/'+path);
        this.state1 = "";
      },
      loadAll() {
        return [
          { "value": "[8143]新增问卷信息" },
          { "value": "[8140]评估问卷明细查询维护" },
          { "value": "[8138]问卷控制信息查询维护" },
          { "value": "[8084]客户经理信息查询维护" },
          { "value": "[8045]理财产品管理表查询维护" },
          { "value": "[8007]理财产品信息查询维护" },
          { "value": "[8040]理财产品excel导入" },
          { "value": "[8050]理财产品销售控制查询维护" },
          { "value": "[8012]理财产品运行期维护" },
          { "value": "[8025]母产品信息查询维护" },
          { "value": "[8031]理财产品转换配置查询维护" },
          { "value": "[8028]理财产品净值查询维护" },
          { "value": "[8035]理财产品销售区域查询维护" },
          { "value": "[8014]理财产品额度信息查询维护" },
          { "value": "[8210]理财产品收费信息查询维护" },
          { "value": "[8057]机构管理" },
          { "value": "[8053]柜员信息查询维护" },
          { "value": "[8056]角色信息查询维护" },
          { "value": "[8064]柜员角色关系查询维护" },
          { "value": "[8058]角色权限查询维护" },
          { "value": "[8060]柜员交易权限查询" },
          { "value": "[8061]柜员密码修改" },
          { "value": "[8062]柜员密码重置" },
          { "value": "[8029]存期分档收益率查询维护" },
          { "value": "[8046]净值产品分红配置信息查询维护" },
          { "value": "[8202]分红预设查询维护" },
          { "value": "[8209]资金分档收益率查询维护" }
        ];
      }
    },
    mounted() {
      this.mission = this.loadAll();
    }
  }
</script>



