<style lang="stylus" rel="stylesheet/stylus">
  .index
    .main
      h1,h2
        text-align center
        font-weight bold
        line-height 1.5
      h1
        font-size 30px
      .el-table+h1
        margin-top 20px
      h2
        font-size 20px
        padding-bottom 20px
      h3
        font-siez 18px
        font-weight bold
        line-height 3
      h4
        font-size 14px
        line-height 4
        text-align center
        border-top 1px solid #eee
        span
          font-weight bold
          color #58B7FF
      .el-collapse
        border none
        .el-collapse-item__header
          -webkit-transition all .3s
          -moz-transition all .3s
          -ms-transition all .3s
          -o-transition all .3s
          transition all .3s
          text-align right
          padding 0 20px
          font-size 15px
          color #8391a5
          .el-collapse-item__header__arrow
            float right
            margin 14px 0 0 8px
            -ms-transform  rotate(-90deg)
            transform  rotate(-90deg)
        .el-collapse-item__header:hover
          background #58B7FF
          color #fff
        .el-collapse-item__wrap
          padding-top 10px
          .el-slider__bar
            background #67C23A
          .el-textarea
            textarea
              minheight 40px!important
              line-height 28px
          /*.el-rate
            height:40px
            display flex
            align-items center
            .el-rate__text
              display inline-block
              min-width 40px
              text-align right*/
        .el-collapse-item.is-active>.el-collapse-item__header
          color #20a0ff
        .el-collapse-item.is-active>.el-collapse-item__header:hover
          background #D3DCE6
          color #333
        .el-collapse-item.is-active>.el-collapse-item__header .el-collapse-item__header__arrow
          -ms-transform  rotate(90deg)
          transform  rotate(90deg)
        .el-button
          margin-bottom 20px
</style>

<template>
  <div class="index">
    <div class="main">
      <el-collapse v-model="collapesName" :accordion="true">
        <!--自评块-->
        <el-collapse-item :title="evlOrg+'-部门自评'" name="1"><!-- v-show="role<1"-->
          <h4>请分别为"专业能力"，"责任意识"，"协作效率"，"最终结果"填写0-100的得分，目前总分：<span>{{selfTotle}}</span></h4>
          <el-row type="flex">
            <el-form ref="form" :model="selfData" label-width="150px" class="el-col el-col-21">
              <el-form-item label="专业能力" prop="skill" class="el-col el-col-6">
                <el-slider v-model="selfData.skill" show-input></el-slider>
              </el-form-item>
              <el-form-item label="责任意识" prop="realize" class="el-col el-col-6">
                <el-slider v-model="selfData.realize" show-input></el-slider>
              </el-form-item>
              <el-form-item label="协作效率" prop="effect" class="el-col el-col-6">
                <el-slider v-model="selfData.effect" show-input></el-slider>
              </el-form-item>
              <el-form-item label="最终结果" prop="result" class="el-col el-col-6">
                <el-slider v-model="selfData.result" show-input></el-slider>
              </el-form-item>
              <el-form-item label="评价说明" prop="text" class="el-col el-col-24">
                <el-input type="textarea" autosize placeholder="请输入内容" v-model="selfData.text"></el-input>
              </el-form-item>
            </el-form>
            <div class="el-col el-col-2 el-col-push-1">
              <el-progress type="dashboard" :color="colorPro" width="100"
                           :percentage="selfTotle"
              ></el-progress>
            </div>
          </el-row>
          <el-row type="flex" justify="center">
            <el-button type="success" plain @click="saveState">保存</el-button>
            <el-button type="success" @click="commitState">提交</el-button>
          </el-row>
        </el-collapse-item>
        <!--互拼块-->
        <el-collapse-item :title="otherTitle" name="2">
          <div v-for="(item,index) in otherData" :key="index">
            <h4>请为 <span>{{item.orgname}}</span> 评分，目前总分：
              <span>{{item.skill+item.realize+item.effect+item.result}}</span>
            </h4>
            <el-row type="flex">
              <el-form ref="form" :model="item" label-width="150px" class="el-col el-col-21">
                <el-form-item label="专业能力" prop="skill" class="el-col el-col-6">
                  <el-slider v-model="item.skill" show-input></el-slider>
                </el-form-item>
                <el-form-item label="责任意识" prop="realize" class="el-col el-col-6">
                  <el-slider v-model="item.realize" show-input></el-slider>
                </el-form-item>
                <el-form-item label="协作效率" prop="effect" class="el-col el-col-6">
                  <el-slider v-model="item.effect" show-input></el-slider>
                </el-form-item>
                <el-form-item label="最终结果" prop="result" class="el-col el-col-6">
                  <el-slider v-model="item.result" show-input></el-slider>
                </el-form-item>
                <el-form-item label="评价说明" prop="text" class="el-col el-col-24">
                  <el-input type="textarea" autosize placeholder="请输入内容" v-model="item.text"></el-input>
                </el-form-item>
              </el-form>
              <div class="el-col el-col-2 el-col-push-1">
                <el-progress type="dashboard" :color="colorPro" width="100"
                             :percentage="parseInt(item.skill*.2+item.realize*.2+item.effect*.3+item.result*.3)"
                ></el-progress>
              </div>
            </el-row>
          </div>
          <el-row type="flex" justify="center">
            <el-button type="success" plain @click="saveState">保存</el-button>
            <el-button type="success" @click="commitState">提交</el-button>
          </el-row>
        </el-collapse-item>
        <!--表格块-->
        <el-collapse-item title="统计表格" name="3"><!-- v-show="role>1"-->
          <h1>{{maintitle}}协作满意度考核</h1>
          <h2>({{year}})</h2>
          <el-table ref="singleTable" :data="table1Data" border style="width: 100%" show-summary="true" sum-text="汇总"
                    highlight-current-row @row-click="handleclick" stripe="true">
            <el-table-column type="index" width="65"></el-table-column>
            <el-table-column prop="type" label="项目"></el-table-column>
            <el-table-column label="评价内容 ">
              <el-table-column prop="selevl" label="自评"></el-table-column>
              <el-table-column prop="othevl" label="部室互评"></el-table-column>
              <el-table-column prop="subevl" label="分行评价"></el-table-column>
              <el-table-column prop="maievl" label="行领导评价"></el-table-column>
            </el-table-column>
            <el-table-column label="评分结果">
              <el-table-column prop="total" label="总分"></el-table-column>
              <el-table-column prop="level" label="相应格次"></el-table-column>
            </el-table-column>
          </el-table>
          <h1>总行各部室跨部门协作满意度考核结果汇总表</h1>
          <h2>({{year}})</h2>
          <el-table ref="singleTable" :data="table2Data" border style="width: 100%"
                    highlight-current-row @row-click="handleclick" stripe="true">
            <el-table-column type="index" width="65"></el-table-column>
            <el-table-column prop="border" label="板块"></el-table-column>
            <el-table-column prop="depart" label="总行部室"></el-table-column>
            <el-table-column label="评分方式及得分">
              <el-table-column prop="selevl" label="自评"></el-table-column>
              <el-table-column prop="othevl" label="部室互评"></el-table-column>
              <el-table-column prop="subevl" label="分行评价"></el-table-column>
              <el-table-column prop="maievl" label="行领导评价"></el-table-column>
            </el-table-column>
            <el-table-column label="评分结果">
              <el-table-column prop="total" label="总分"></el-table-column>
              <el-table-column prop="level" label="相应格次"></el-table-column>
            </el-table-column>
          </el-table>
        </el-collapse-item>
      </el-collapse>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        maintitle:'跨部门',
        year:'2019',
        //折叠面板
        collapesName: [],
        otherTitle:'',
        evlOrg:'信息科技部',
        //登录人员权限
        role:"0",
        //颜色设置
        colorPro:[{color: '#99A9BF', percentage: 33}, {color: '#F7BA2A', percentage: 66}, {color: '#FF9900', percentage: 100}],
        //表格当前页数据
        table1Data:[
          {type:'自评', selevl:'40', othevl:'40', subevl:'40', maievl:'40', total:'160', level:''},
          {type:'部室互拼', selevl:'40', othevl:'40', subevl:'40', maievl:'40', total:'160', level:''},
          {type:'分行评价', selevl:'40', othevl:'40', subevl:'40', maievl:'40', total:'160', level:''},
          {type:'行领导评价', selevl:'40', othevl:'40', subevl:'40', maievl:'40', total:'160', level:''},
        ],
        table2Data:[
          {border:'公司金融板块', depart:'董事会办公室', selevl:'40', othevl:'40', subevl:'40', maievl:'40', total:'160', level:''},
          {border:'零售金融板块', depart:'监事会办公室', selevl:'40', othevl:'40', subevl:'40', maievl:'40', total:'160', level:''},
          {border:'金融市场板块', depart:'办公室(党委办公室)', selevl:'40', othevl:'40', subevl:'40', maievl:'40', total:'160', level:''},
          {border:'风险管理板块', depart:'人力资源部(党委组织部)', selevl:'40', othevl:'40', subevl:'40', maievl:'40', total:'160', level:''},
        ],
        //弹出框表单禁止输入
        edBlock: false,
        //自评数据
        selfData:{skill:null,realize:null,effect:null,result:null},
        //互评数据
        otherData:[
          {orgname:"董事会办公室", skill:"", realize:"", effect:"", result:""},
          {orgname:"监事会办公室", skill:"", realize:"", effect:"", result:""},
          {orgname:"办公室(党委办公室)", skill:"", realize:"", effect:"", result:""},
          {orgname:"人力资源部(党委组织部)", skill:"", realize:"", effect:"", result:""},
          {orgname:"培训部", skill:"", realize:"", effect:"", result:""},
          {orgname:"财务会计部", skill:"", realize:"", effect:"", result:""},
          {orgname:"资产负载管理部 ", skill:"", realize:"", effect:"", result:""},
          {orgname:"发展规划部", skill:"", realize:"", effect:"", result:""},
          {orgname:"数字银行管理部", skill:"", realize:"", effect:"", result:""},
          {orgname:"党群工作部", skill:"", realize:"", effect:"", result:""},
          {orgname:"安全保卫部", skill:"", realize:"", effect:"", result:""},
          {orgname:"行政事务部", skill:"", realize:"", effect:"", result:""},
          {orgname:"科教创新园服务中心", skill:"", realize:"", effect:"", result:""},
        ],

        //默认高亮行数据
        highlightRow: "",

      }
    },
    methods: {
      saveState(){

      },
      commitState(){

      },

      //点击行响应
      handleclick: function(row, event, column){
        this.highlightRow = row;
      },
    },
    computed: {
      selfTotle(){
        return parseInt(this.selfData.skill*.2+this.selfData.realize*.2+this.selfData.effect*.3+this.selfData.result*.3)
      },
    },
    mounted() {
//        util.api.post(url).then(function(data){
//          console.log(data)
//        )
      //根据角色权限，显示默认展示栏
      if(this.role<1){
        this.otherTitle= "其他部门互评";
        this.collapesName = ['1']
      }else{
        this.otherTitle= "部门评价";
        this.collapesName = ['2']
      }
    },
  }
</script>



