<style lang="stylus" rel="stylesheet/stylus">
  .login
    position fixed
    display flex
    top 0
    right 0
    bottom 0
    left 0
    background-color #37c6c0
    canvas
      position fixed
    .login-box
      margin auto
      width 300px
      height 250px
      border-radius 10px 10px 15px 15px
      background-color #ffffff
      padding 30px 10px
      border-bottom:5px solid #56a36c
      .login-box-title
        color #37c6c0
        margin-bottom 30px
        font-size 23px
        font-weight 500
        text-align center
      .el-form
        .el-input
          border-radius 0
        .captcha
          float right
          width 95%
          height 36px
    .login-box-button
      margin-top 15px
      width 100%
</style>

<template>
  <div class="login">
    <canvas id="canvas"></canvas>
    <div class="login-box">
      <h2 class="login-box-title">南京银行-考评系统</h2>
      <el-form :model="login" :rules="rules" ref="login">
        <el-row type="flex" justify="center">
          <el-col :span="18">
            <el-form-item prop="guiydaih">
              <el-input v-model.number="login.guiydaih" size="large" placeholder="用户名" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row type="flex" justify="center">
          <el-col :span="18">
            <el-form-item prop="guiymima">
              <el-input v-model="login.guiymima" size="large" placeholder="密码" type="password"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <el-row type="flex" justify="center">
        <el-col :span="18">
          <el-button class="login-box-button" type="success" :loading="loading" @click="loginSubmit">
            登录
          </el-button>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
  import doCanvas from '../../assets/js/canvas'
  import util from '../../assets/js/util'
  export default{
    data(){
      return {
        login: {
          guiydaih: '',
          guiymima: '',
          captcha: ''
        },
        rules: {
          guiydaih: [
            {required: true, message: '请输入帐号'},
            {pattern:/^\d{8}$/ , message: '帐号必须为8位的数字', trigger: 'blur'}
          ],
          guiymima: [
            {required: true, message: '请输入密码'},
            { max: 32, message: '长度在 32 个字符以内'}
          ]
        },
        loading: false
      }
    },
    methods: {
      loginSubmit(){
        this.loading = true
//        util.api.post(url).then(function(data){
//          console.log(data)
//        )
        setTimeout(()=>{
          util.authcode='123456'
          util.setSession('authcode',util.authcode)
          this.$router.push('/layout/home')
          this.loading = true
          window.onresize=null
        },500)
      }
    },
    mounted(){
      doCanvas()
      window.onresize=function () {
        doCanvas()
      }
    }
  }
</script>




